<template>
	<div id="finance-index">
		<public-header title="资产"></public-header>
		<div class="content">
			<div class="account bg-info">
				<div class="font-14 margin-15-b">账户可用余额(元)</div>
				<div>¥{{account.amount}}</div>
			</div>
			<van-cell-group class="margin-10-t">
				<van-cell title="保证金" icon="lock" :value="'￥' + account.deposit" ></van-cell>
				<van-cell title="账户明细" icon="account" value="" is-link :to="util.getUrl({path: 'pages/finance/current'})"></van-cell>
				<van-cell title="提现记录" icon="text1" value="" is-link :to="util.getUrl({path: 'pages/finance/getcashRecord'})"></van-cell>
			</van-cell-group>
			<van-cell-group class="margin-10-t">
				<van-cell title="申请提现" icon="refund" value="" is-link :to="util.getUrl({path: 'pages/finance/getCash'})"></van-cell>
			</van-cell-group>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				account: {},
				showPreLoading: true
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'manage/finance/getcash/index',
					autoAssign: true,
					variable: 'account'
				})
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#finance-index .account{
		text-align: center;
		font-size: 30px;
		padding: 40px 0;
	}
</style>


